"use client";

import React, { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";

const appointmentsData = [
  {
    id: 1,
    patient: "John Smith",
    dentist: "Dr. Sarah Johnson",
    date: "2024-01-20",
    time: "09:00 AM",
    treatment: "Dental Cleaning",
    status: "Confirmed",
    duration: "30 min",
    notes: "Regular checkup and cleaning",
  },
  {
    id: 2,
    patient: "Emily Davis",
    dentist: "Dr. Michael Brown",
    date: "2024-01-20",
    time: "10:30 AM",
    treatment: "Root Canal",
    status: "Confirmed",
    duration: "90 min",
    notes: "Follow-up root canal treatment",
  },
  {
    id: 3,
    patient: "Robert Wilson",
    dentist: "Dr. Sarah Johnson",
    date: "2024-01-20",
    time: "02:00 PM",
    treatment: "Tooth Extraction",
    status: "Pending",
    duration: "45 min",
    notes: "Wisdom tooth extraction",
  },
  {
    id: 4,
    patient: "Sarah Johnson",
    dentist: "Dr. Michael Brown",
    date: "2024-01-21",
    time: "11:00 AM",
    treatment: "Dental Crown",
    status: "Confirmed",
    duration: "60 min",
    notes: "Crown placement for molar",
  },
];

const dentists = ["Dr. Sarah Johnson", "Dr. Michael Brown", "Dr. Lisa Anderson"];
const treatments = ["Dental Cleaning", "Root Canal", "Tooth Extraction", "Dental Crown", "Filling", "Whitening"];
const timeSlots = ["09:00 AM", "10:00 AM", "11:00 AM", "02:00 PM", "03:00 PM", "04:00 PM"];

export default function AppointmentsPage() {
  const [selectedDate, setSelectedDate] = useState("2024-01-20");
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const filteredAppointments = appointmentsData.filter(
    appointment => appointment.date === selectedDate
  );

  const handleScheduleAppointment = (e: React.FormEvent) => {
    e.preventDefault();
    // Handle form submission here
    setIsDialogOpen(false);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case "Confirmed":
        return "default";
      case "Pending":
        return "secondary";
      case "Cancelled":
        return "destructive";
      default:
        return "secondary";
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Appointments</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button>Schedule Appointment</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[500px]">
            <DialogHeader>
              <DialogTitle>Schedule New Appointment</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleScheduleAppointment} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="patientName">Patient Name</Label>
                  <Input id="patientName" placeholder="John Smith" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="dentist">Dentist</Label>
                  <Select required>
                    <SelectTrigger>
                      <SelectValue placeholder="Select dentist" />
                    </SelectTrigger>
                    <SelectContent>
                      {dentists.map((dentist) => (
                        <SelectItem key={dentist} value={dentist}>
                          {dentist}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>
              
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="date">Date</Label>
                  <Input id="date" type="date" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="time">Time</Label>
                  <Select required>
                    <SelectTrigger>
                      <SelectValue placeholder="Select time" />
                    </SelectTrigger>
                    <SelectContent>
                      {timeSlots.map((time) => (
                        <SelectItem key={time} value={time}>
                          {time}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="treatment">Treatment</Label>
                <Select required>
                  <SelectTrigger>
                    <SelectValue placeholder="Select treatment" />
                  </SelectTrigger>
                  <SelectContent>
                    {treatments.map((treatment) => (
                      <SelectItem key={treatment} value={treatment}>
                        {treatment}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="duration">Duration</Label>
                <Select required>
                  <SelectTrigger>
                    <SelectValue placeholder="Select duration" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="30">30 minutes</SelectItem>
                    <SelectItem value="45">45 minutes</SelectItem>
                    <SelectItem value="60">60 minutes</SelectItem>
                    <SelectItem value="90">90 minutes</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="notes">Notes</Label>
                <Textarea id="notes" placeholder="Additional notes..." />
              </div>

              <div className="flex justify-end space-x-2">
                <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>
                  Cancel
                </Button>
                <Button type="submit">Schedule Appointment</Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Date Filter */}
      <Card>
        <CardContent className="pt-6">
          <div className="flex items-center space-x-4">
            <Label htmlFor="dateFilter">Filter by Date:</Label>
            <Input
              id="dateFilter"
              type="date"
              value={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              className="max-w-xs"
            />
            <Badge variant="secondary">
              {filteredAppointments.length} appointments
            </Badge>
          </div>
        </CardContent>
      </Card>

      {/* Appointments List */}
      <div className="space-y-4">
        {filteredAppointments.map((appointment) => (
          <Card key={appointment.id} className="hover:shadow-lg transition-shadow">
            <CardContent className="pt-6">
              <div className="flex justify-between items-start">
                <div className="flex-1 space-y-2">
                  <div className="flex items-center space-x-4">
                    <h3 className="text-lg font-semibold text-gray-900">
                      {appointment.patient}
                    </h3>
                    <Badge variant={getStatusColor(appointment.status)}>
                      {appointment.status}
                    </Badge>
                  </div>
                  
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 text-sm text-gray-600">
                    <div>
                      <span className="font-medium">Time:</span> {appointment.time}
                    </div>
                    <div>
                      <span className="font-medium">Duration:</span> {appointment.duration}
                    </div>
                    <div>
                      <span className="font-medium">Dentist:</span> {appointment.dentist}
                    </div>
                    <div>
                      <span className="font-medium">Treatment:</span> {appointment.treatment}
                    </div>
                  </div>

                  {appointment.notes && (
                    <div className="text-sm text-gray-600">
                      <span className="font-medium">Notes:</span> {appointment.notes}
                    </div>
                  )}
                </div>

                <div className="flex space-x-2 ml-4">
                  <Button size="sm" variant="outline">
                    Edit
                  </Button>
                  <Button size="sm" variant="outline">
                    Cancel
                  </Button>
                  <Button size="sm">
                    Complete
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {filteredAppointments.length === 0 && (
        <Card>
          <CardContent className="text-center py-8">
            <p className="text-gray-500">No appointments scheduled for this date.</p>
            <Button className="mt-4" onClick={() => setIsDialogOpen(true)}>
              Schedule First Appointment
            </Button>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
